@import url(https://fonts.googleapis.com/css?family=Eczar&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css?family=Kanit&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.projects-each {
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0 0 5px rgb(0, 0, 0);
}
.projects-each > div {
    flex: 1 1;
}
.image-wrapper img {
    display: flex;
    height: 40vh;
    width: 100%;
    padding: 15px;
    margin-top: 20px;
}
.project-info {
    display: flex;
    flex-direction: column;
    width: 400px;
    height: auto;
    padding: 15px;
    font-size: 20px;
    font-family: 'Kanit', sans-serif;
    color: black;
}
.projects > div {
    flex: 0 1 50%;
    box-sizing:border-box;
    display: flex;
    justify-content: space-evenly;
}
.project-title{
    border-top: solid 0.5pc #4D9DEC;
    display: flex;
    justify-content: center;
    font-weight: bold;
    font-size: 30px;
    color: black;
}
.project-link a{
    color: black;
} 
.project-title:hover{
    color: white;
    background-color: #4D9DEC;
    text-decoration: underline;
}
.project-code {
    display: flex;
    justify-content: center;
}
.project-code a:hover{
    background-color: #4D9DEC;
    color: white;
    text-decoration: none;
}
@media only screen and (max-width: 600px){
    .projects-each {
        width: 100vh;
        height: 140vh; 
        margin: 10%;
    }
    .project-info {
        display: flex;
        flex-direction: column;
        margin-left: 30px;
        width: 730px;
        height: auto;
        padding: 3px;
        font-size: 30px;
        font-family: 'Kanit', sans-serif;
        color: black;
    }
    .image-wrapper img {
        display: flex;
        height: 85vh;
        padding: 20px;
        margin-top: 20px;
    }
    
}



html {
    scroll-behavior: smooth;
    margin: 0;
    width: 100%;
}
#project-container {
    display: flex;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr;
    width: 10%;
    justify-items: center;
    align-content: center;
}
.projects {
    display: flex;
    justify-content: center;
    margin-top: 10%;
    margin-bottom: 10%;
    flex: 1 1;
}
.page-body {
    box-shadow: 0 0 10px rgb(0, 0, 0);
    margin: 2pc;
    background-color: white;
}
.html-body {
    padding-top: 2pc;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-image: -webkit-gradient(linear, left bottom, right top, from(#052d55), color-stop(#15406f), color-stop(#23548a), color-stop(#3069a7), color-stop(#3d7fc4), color-stop(#3491d7), color-stop(#25a4ea), color-stop(#00b7fb), color-stop(#00c9fa), color-stop(#00d7de), color-stop(#00e0a8), to(#1be462));
    background-image: linear-gradient(to right top, #052d55, #15406f, #23548a, #3069a7, #3d7fc4, #3491d7, #25a4ea, #00b7fb, #00c9fa, #00d7de, #00e0a8, #1be462);
}
.about-me-body {
    box-shadow: 0 0 10px rgb(0, 0, 0);
    margin: 2pc;
    background-color: white;
}
.projects-body {
    box-shadow: 0 0 10px rgb(0, 0, 0);
    margin: 2pc;
    background-color: white;
    padding-top: 1ch;
    padding-bottom: .5pc;
}
@media only screen and (max-width: 600px) {
    #project-container {
        display: flex;
        flex-direction: column;
    }
    .projects {
        margin-right: 80%;
    }
  }





.header {
    height: 650px;
    padding-top: 50px;
    background-image: url(/static/media/headerimg.ef3672ea.JPG);
}
.title{
  position: relative;
  top: 20%;  
  color: white;
  margin: 5%;
  text-shadow: 0 0 4px rgb(3, 3, 3);
}
.title h1{
  font-size: 4pc;
  font-weight: bold;
}
.title h2 {
  font-size: 2pc;
  font-weight: bold;
}


.navbar{
  border-top: #4D9DEC 1pc solid;
}
.nav-link {
  color: black;
  text-decoration: none;
  font-weight: bold;
}
.navbar a:hover {
    background-color: #4D9DEC;
    color: white;
}

#footer {
    padding: 3pc;
    display: flex;
    justify-content: space-around;
    height: 500px;
    background-color: #0D1C30;
}
#title {
    display: flex;
    justify-content: center;
    color: white;
    font-size: 2pc;
}
.contact-form{
    display: flex;
    flex-direction: column;
    margin: 25px;
}
.contact-form textarea{
    font-size: 20px;
    margin-top: 20px;
    width: 50vh;
    height: 12pc;
}
.zoom{
    display: flex;
    justify-content: center;
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
} 
.zoom:hover { /* IE 9 */
    -webkit-transform: scale(1.2); /* Safari 3-8 */
    transform: scale(1.2);  
} 
.contact-links a{
    color: white;
} 
.contact-links a:hover{
    color: greenyellow;
    border-radius: 30px;
    text-decoration: none;
} 
.contact-links {
    font-size: 4vh;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
}
.contact-links img{
    height: auto;
    width: 3pc;

}
 @media only screen and (max-width: 600px){
    body {
      width: 920px;
    }
    img {
        height: 170px;
    }
    .contact-form{
        display: flex;
        flex-direction: column;
        width: 80%;
    }
    .contact-links {
        margin-left: 100px;
    }
  }

#about-me {
    display: flex;
    flex-direction: row;
    background-color: #4D9DEC;
    font-size: 3vh;
    margin-top: 5%;
    font-weight: bold;
    color: white;
    padding: 60px;
    padding-top: 30px;
    border-top: 2pc solid white;
}
#about-me h3 {
    overflow: hidden;
    text-align: center;
    font-weight: bold;
    font-size: 2.5pc;
    margin-bottom: 5%;
   }

.about-me-images h3{
    font-size: 20px;
    margin: 30px;
    text-align: center;
}

.about-me-images img{
    height: 6pc;
    padding: 10px;
    border-radius: 1em/.5em; 
    margin: 1rem;
    background-color: rgba(255, 255, 255, 0.733);
}
#self-portrait { 
    margin: auto;
    border: .5pc solid white;
    border-radius: 50%;
}
